@use 'additional/mixins' as *;
.footer {
  padding: 50px 0;

  &__title {
    margin-bottom: 20px;
  }

  &__list {
    display: flex;
    gap: 20px;
  }

  &__link {
    border-radius: 100vmax;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: rgba(#fff, 0.5);
    transition: 0.2s linear;

    svg {
      width: 30px;
      height: 30px;
      transition: 0.2s linear;
      stroke: #0000;
    }

    &:hover {
      background-color: #fff;
    }
  }
}

@include breakpoint-down(medium) {
  .footer {
    padding: 25px;

    &__title {
      text-align: center;
    }

    &__list {
      justify-content: center;
    }
  }
}

@include breakpoint-down(regular) {
  .footer {
    padding: 25px 0 75px;
  }
}
